body {
  margin: 0;
  font-weight: 300;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  background-color: #3b2a17;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: none;
  min-width: 320px; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none;
  color: #000;
  transition: all 0.2s ease-out;
  -webkit-tap-highlight-color: transparent; }

a:hover,
a:focus {
  color: inherit;
  text-decoration: none; }

img {
  max-width: 100%; }

textarea {
  width: 100%;
  resize: vertical;
  border: none;
  outline: none;
  border-radius: 0; }

label {
  margin: 0;
  -webkit-tap-highlight-color: transparent; }

input, button, select {
  border: none;
  outline: none; }

input[type=text], input[type=submit], textarea {
  -webkit-appearance: none; }

input {
  border-radius: 0;
  outline: none;
  box-shadow: none; }

button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent; }

